

fieldset[disabled] .multiselect {
  pointer-events: none;
}

.multiselect__loading-enter-active,.multiselect__loading-leave-active {
  transition: opacity .4s ease-in-out;
  opacity: 1;
}

.multiselect__loading-enter,.multiselect__loading-leave-active {
  opacity: 0;
}

.multiselect,.multiselect__input,.multiselect__single {
  font-family: inherit;
  font-size: 1em;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

.multiselect {
  display: inline-block;
  max-width: 100%;
  position: relative;
  vertical-align: top;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.multiselect:hover .multiselect__tags {
  border-color: #b5b5b5;
}

.multiselect:hover .multiselect__select::before,.multiselect.multiselect--active .multiselect__select::before {
  border-color: #363636;
}

.multiselect--disabled {
  opacity: 0.6;
}

.multiselect--active {
  z-index: 20;
}

.multiselect--active .multiselect__select {
  transform: rotate(180deg);
}

.multiselect--active .multiselect__tags,.multiselect--active:hover .multiselect__tags {
  border-color: #3273dc;
  box-shadow: 0 0 0 0.125em rgba(50, 115, 220, 0.25);
}

.multiselect__input,.multiselect__single {
  position: relative;
  display: inline-block;
  min-height: 1.5;
  line-height: 1.5;
  border: none;
  border-radius: 4px;
  background: white;
  padding: 0;
  width: 100%;
  transition: border .1s ease;
  box-sizing: border-box;
  vertical-align: top;
}

.multiselect__input:-ms-input-placeholder,.multiselect__input::placeholder {
  color: #35495e;
}

.multiselect__input,.multiselect__single {
  width: auto;
}

.multiselect__input:focus {
  outline: none;
}

.multiselect__single:focus {
  outline: none;
}

.multiselect__tags-wrap {
  display: inline;
}

.multiselect__tags {
  min-height: 2.25em;
  display: block;
  padding: calc(0.375em - 1px) 2.5em calc(0.375em - 1px) 0.75em;
  border-radius: 4px;
  border: 1px solid #dbdbdb;
  background: white;
  line-height: 1;
}

.multiselect__tag {
  position: relative;
  display: inline-block;
  padding: 2px 26px 4px 10px;
  border-radius: 5px;
  margin-right: 10px;
  color: #fff;
  line-height: 1;
  background: #41b883;
  white-space: nowrap;
  overflow: hidden;
  max-width: 100%;
  text-overflow: ellipsis;
}

.multiselect__tag-icon {
  cursor: pointer;
  margin-left: 7px;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  font-weight: 700;
  font-style: normal;
  width: 22px;
  text-align: center;
  line-height: 16px;
  transition: all .2s ease;
  border-radius: 5px;
}

.multiselect__tag-icon:after {
  content: "\D7";
  color: #266d4d;
  font-size: 14px;
}

.multiselect__tag-icon:focus,.multiselect__tag-icon:hover {
  background: #369a6e;
}

.multiselect__tag-icon:focus:after,.multiselect__tag-icon:hover:after {
  color: #fff;
}

.multiselect__current {
  min-height: 40px;
  overflow: hidden;
  padding: 8px 30px 0 12px;
  white-space: nowrap;
  border-radius: 5px;
  border: 1px solid #e8e8e8;
  line-height: 16px;
  box-sizing: border-box;
  display: block;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
}

.multiselect__select,.multiselect__spinner {
  position: absolute;
  width: 2.85em;
  height: calc(2.25em - 2px);
  right: 1px;
  top: 1px;
  border-radius: 4px;
  display: block;
}

.multiselect__select {
  cursor: pointer;
  transition: transform 0.2s ease;
}

.multiselect__select:before {
  border: 3px solid #3273dc;
  border-radius: 2px;
  border-right: 0;
  border-top: 0;
  content: " ";
  display: block;
  height: 0.625em;
  margin-top: -0.4375em;
  pointer-events: none;
  position: absolute;
  top: 1.15em;
  transform: rotate(-45deg);
  transform-origin: center center;
  width: 0.625em;
  right: 1.125em;
}

.multiselect__spinner {
  background: white;
}

.multiselect__spinner:after {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  margin: -8px 0 0 -8px;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  border: 2px solid transparent;
  border-top-color: #3273dc;
  box-shadow: 0 0 0 1px transparent;
}

.multiselect__spinner:before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  margin: -8px 0 0 -8px;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  border: 2px solid transparent;
  border-top-color: #3273dc;
  box-shadow: 0 0 0 1px transparent;
  animation: a 2.4s cubic-bezier(0.41, 0.26, 0.2, 0.62);
  animation-iteration-count: infinite;
}

.multiselect__spinner:after {
  animation: a 2.4s cubic-bezier(0.51, 0.09, 0.21, 0.8);
  animation-iteration-count: infinite;
}

.multiselect__placeholder {
  color: #363636;
  display: inline-block;
  line-height: 1.5;
}

.multiselect--active .multiselect__placeholder {
  display: none;
}

.multiselect__content-wrapper {
  position: absolute;
  display: block;
  background: white;
  width: 100%;
  max-height: 240px;
  overflow: auto;
  box-shadow: 0 2px 3px rgba(10, 10, 10, 0.1), 0 0 0 1px rgba(10, 10, 10, 0.1);
  margin-top: 4px;
  border-radius: 4px;
  z-index: 20;
  -webkit-overflow-scrolling: touch;
}

.multiselect__content {
  list-style: none;
  display: inline-block;
  padding: 0;
  margin: 0;
  min-width: 100%;
  vertical-align: top;
}

.multiselect--above .multiselect__content-wrapper {
  bottom: 100%;
  margin-bottom: 4px;
}

.multiselect__content::webkit-scrollbar {
  display: none;
}

.multiselect__element {
  display: block;
}

.multiselect__option {
  display: block;
  padding: 12px;
  min-height: 40px;
  line-height: 16px;
  text-decoration: none;
  text-transform: none;
  vertical-align: middle;
  position: relative;
  cursor: pointer;
  white-space: nowrap;
}

.multiselect__option:after {
  top: 0;
  right: 0;
  position: absolute;
  line-height: 40px;
  padding-right: 12px;
  padding-left: 20px;
  font-size: 1em;
}

.multiselect__option--highlight {
  background: #41b883;
  outline: none;
  color: #fff;
}

.multiselect__option--highlight:after {
  content: attr(data-select);
  background: #41b883;
  color: #fff;
}

.multiselect__option--selected {
  background: #f3f3f3;
  color: #35495e;
  font-weight: 700;
}

.multiselect__option--selected:after {
  content: attr(data-selected);
  color: silver;
}

.multiselect__option--selected.multiselect__option--highlight {
  background: #ff6a6a;
  color: #fff;
}

.multiselect__option--selected.multiselect__option--highlight:after {
  background: #ff6a6a;
  content: attr(data-deselect);
  color: #fff;
}

.multiselect--disabled {
  background: #ededed;
  pointer-events: none;
}

.multiselect--disabled .multiselect__current,.multiselect--disabled .multiselect__select {
  background: #ededed;
  color: #a6a6a6;
}

.multiselect__option--disabled {
  background: #ededed !important;
  color: #a6a6a6 !important;
  cursor: text;
  pointer-events: none;
}

.multiselect__option--group {
  background: #ededed;
  color: #35495e;
}

.multiselect__option--group.multiselect__option--highlight {
  background: #35495e;
  color: #fff;
}

.multiselect__option--group.multiselect__option--highlight:after {
  background: #35495e;
}

.multiselect__option--disabled.multiselect__option--highlight {
  background: #dedede;
}

.multiselect__option--group-selected.multiselect__option--highlight {
  background: #ff6a6a;
  color: #fff;
}

.multiselect__option--group-selected.multiselect__option--highlight:after {
  background: #ff6a6a;
  content: attr(data-deselect);
  color: #fff;
}

.multiselect-enter-active,.multiselect-leave-active {
  transition: all 0.15s ease;
}

.multiselect-enter,.multiselect-leave-active {
  opacity: 0;
}

.multiselect__strong {
  margin-bottom: 8px;
  line-height: 20px;
  display: inline-block;
  vertical-align: top;
}

[dir=rtl] .multiselect {
  text-align: right;
}

[dir=rtl] .multiselect__select {
  right: auto;
  left: 1px;
}

[dir=rtl] .multiselect__tags {
  padding: 8px 8px 0 40px;
}

[dir=rtl] .multiselect__content {
  text-align: right;
}

[dir=rtl] .multiselect__option:after {
  right: auto;
  left: 0;
}

[dir=rtl] .multiselect__clear {
  right: auto;
  left: 12px;
}

[dir=rtl] .multiselect__spinner {
  right: auto;
  left: 1px;
}

.multiselect.is-fullwidth {
  width: 100%;
}

.multiselect.is-small {
